.normal {
    width: 65px;
    height: 65px;
    border: none;
    font-size: 35px;
    text-align: center;
    border-radius: 100%;
    background: #e0e0e0;
    box-shadow:  1px 1px 2px #aaa9a9,
                 -1px -1px 2px #c9c7c7;
}

.normal:active {
    background: linear-gradient(145deg, #cacaca, #f0f0f0);
    box-shadow:  1px 1px 2px #aaa9a9,
                -1px -1px 2px #c9c7c7;
}

.bold-green {
    font-weight: 700;
    color: limegreen;
}

.red {
    color: red
}

.equal {
    background: rgb(2, 182, 2);
    box-shadow:  1px 1px 2px rgb(1, 135, 1),
                -1px -1px 2px rgb(2, 198, 2);
    color: white;
}

.equal:active {
    background: linear-gradient(145deg, rgb(1, 147, 1), rgb(2, 205, 2));
    box-shadow:  1px 1px 2px rgb(1, 135, 1),
                -1px -1px 2px rgb(2, 198, 2);
}