.display {
    height: 110px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 12px 12px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}

.display span {
    font-size: 38px;
    font-weight: 400;
    color: white;
    display: block;
    word-wrap: break-word;
    width: 100%;
    text-align: right;
}

.display #result {
    font-size: 25px;
}