.delete {
    font-size: 20px;
    color: limegreen;
    border: none;
    background-color: transparent;
    padding: 5px;
    text-align: center;
    transition: all .1s ease-in-out;
}

.delete:active {
    color: darkred;
    background-color: grey;
    padding: 5px;
    border-radius: 100px;
}

.subdisplay {
    height: 30px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: right;
    align-items: center;
    border-bottom: 2px solid black;
}